import { Line } from "react-chartjs-2";
import { MyBox } from "../../core/MyBox";

import { CategoryScale, Chart, Legend, LinearScale, LineElement, PointElement, Title, Tooltip } from 'chart.js';
import { useCallback, useEffect, useState } from "react";
import { LogCliService } from "../../../services/adminService";
import { MyButton } from "../../core/MyButton";
import { MyFormItem } from "../../core/Input";
import { Col, Row } from "antd";
import moment from "moment";

Chart.register(CategoryScale, LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend);

export default function ChartComponent() {
    const options = {
        responsive: true,
        plugins: {
            legend: {
                position: 'top' as const,
            },
            title: {
                display: true,
                text: 'Count request chart',
            },
        },
    };

    const options2 = {
        responsive: true,
        plugins: {
            legend: {
                position: 'top' as const,
            },
            title: {
                display: true,
                text: 'Load time server chart',
            },
        },
    };

    const [item, setItem] = useState(50);
    const optionSelect = [
        {
            key: 50,
            value: 50,
            label: 50
        },
        {
            key: 100,
            value: 100,
            label: 100
        },
        {
            key: 150,
            value: 150,
            label: 150
        },
        {
            key: 200,
            value: 200,
            label: 200
        },
        {
            key: 250,
            value: 250,
            label: 250
        }
    ]

    const [data, setData] = useState({
        labels: [],
        datasets: [
            {
                label: 'Load time',
                data: [],
                borderColor: 'rgb(255, 99, 132)',
                backgroundColor: 'rgba(255, 99, 132, 0.5)',

            }
        ],
    });

    const [dataLoadTime, setDataLoadTime] = useState({
        labels: [],
        datasets: [
            {
                label: 'Load time',
                data: [],
                borderColor: 'rgb(255, 99, 132)',
                backgroundColor: 'rgba(255, 99, 132, 0.5)',

            }
        ],
    });


    const loadData = useCallback(() => {
        LogCliService.GetChart(item)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    var label = rs.data.listLabels.map((data: any) => {
                        return moment(data).format("DD/MM/YYYY HH:mm:ss");
                    })

                    setData({
                        labels: label,
                        datasets: [
                            {
                                label: 'Load time',
                                data: rs.data.listValueCountRequests,
                                borderColor: 'rgb(255, 99, 132)',
                                backgroundColor: 'rgba(255, 99, 132, 0.5)',

                            }
                        ],
                    })

                    setDataLoadTime({
                        labels: label,
                        datasets: [
                            {
                                label: 'Load time avg',
                                data: rs.data.listValueAvgLoadTimes,
                                borderColor: 'rgb(29, 115, 238)',
                                backgroundColor: 'rgba(29, 115, 238, 0.5)',

                            },
                            {
                                label: 'Load time max',
                                data: rs.data.listValueMaxLoadTimes,
                                borderColor: 'rgb(255, 195, 0)',
                                backgroundColor: 'rgba(255, 195, 0, 0.5)',

                            },
                            {
                                label: 'Load time min',
                                data: rs.data.listValueMinLoadTimes,
                                borderColor: 'rgb(255, 99, 132)',
                                backgroundColor: 'rgba(255, 99, 132, 0.5)',

                            }
                        ],
                    })
                }
            })
    }, [item]);

    useEffect(() => {
        loadData();
        const interval = setInterval(() => {
            loadData();
        }, 5000)

        return (() => {
            clearInterval(interval);
        })
    }, [loadData]);

    return (
        <MyBox title="Biểu đồ">
            <Row>
                <Col span={12}>
                    <MyButton value="Reload Data" onClick={loadData} />
                </Col>
                <Col span={12}>
                    <MyFormItem.Select options={optionSelect} value={item} setValue={setItem} />
                </Col>
            </Row>

            <Line options={options} data={data} />
            <Line options={options2} data={dataLoadTime} />
        </MyBox>
    );
}